/* Universal Styles */

html {
    font-size: 18px;
    background: #f4ecdb;
  }
  
  @media only screen and (max-width: 560px) {
    html {
      font-size: 15px;
    }
  }
  
  input:focus {
    outline: none;
  }
  
  /* Reusable Component - Image Container */
  
  .image-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    background: #000;
  }
  
  .image-container img {
    width: 100%;
  }
  
  /* App Styles */
  
  .App {
    font-family: 'Work Sans', sans-serif;
  }
  
  h1 {
    padding: .66rem 0;
    text-align: center;
    background-color: #013214;
    font-family: Poppins, sans-serif;
    font-size: 1.8rem;
    color: #fff;
  }
h5{
   text-align: right;
}